var app = angular.module('app.factories', [])
.factory('scroll', ['$rootScope',
        function($rootScope) {
            return {
                'bezier': new Bezier (0.25, 0.1, 0.25, 1.0),
                'element': 'main',
                'get': function(){
                    return this.element.scrollTop;
                },
                'set': function(value){
                    this.element.scrollTop = value;
                },
                'animate': function(){
                    if(this.get() !== this.last && this.last !== false) return false;
                    this.n += 1 / this.time / 60;
                    this.set(this.start + this.bezier.get(this.n)*(this.to + 1000));
                    this.last = this.get();
                    if(this.n < 1) requestAnimationFrame(this.animate.bind(this));
                    else if(this.callback) this.callback();
                },
                'animateTo': function(to, time, callback){
                    //console.log(to);
                    this.to = to || 0;

                    if(typeof this.to == 'string'){
                        this.to = {
                            top:0,
                            bottom: this.element.scrollHeight - window.innerHeight,
                            center: (this.element.scrollHeight - window.innerHeight)/2,
                        }[this.to];
                    }

                    if(time===0){
                        this.set(this.to);
                        return false;
                    }
                    this.time = time || 1;

                    this.n = 0;
                    this.callback = callback;
                    this.start = this.get();
                    this.last = false;
                    this.to = Math.min(this.to, this.element.scrollHeight - window.innerHeight);
                    this.to -= this.start;
                    setTimeout(this.animate.bind(this), 0);
                }
            };
        }
    ])
.factory('Drawer',['$timeout',function($timeout){
    var Drawer = function(element, width) {
        this.domElement = element; // –ï–ª–µ–º–µ–Ω –≤—ã–¥–≤–∏–∂–Ω–æ–≥–æ –º–µ–Ω—é
        this.width = width || element.clientWidth - 20; // –õ–∏–º–∏—Ç –≤—ã–¥–≤–∏–∂–µ–Ω–∏—è –º–µ–Ω—é

        this.content = this.domElement.getElementsByClassName('content')[0];
        this.shadow = this.domElement.getElementsByClassName('shadow')[0];

        this.drag = false;
        this.offset = 0;
        this.domElement.addEventListener('mousedown', this.pointerdown.bind(this));
        this.domElement.addEventListener('touchstart', this.pointerdown.bind(this));
        this.shadow.addEventListener('click', this.close.bind(this));
        this.previousEvents = false;
        this.alpha = false;
    };

    Drawer.prototype.set = function(value) {
        if(value) this.offset = value;
        this.shadow.style.display = this.offset>0?'block':'none';
        this.shadow.style.opacity = this.offset/this.width;
        this.domElement.style.left = -this.width + this.offset + 'px';
        //this.content.style.boxShadow = '0px 0px 16px rgba(0,0,0,'+(this.offset/this.width*.2)+')';
        //this.domElement.children[0].style.boxShadow= '0 0 16px rgba(0,0,0,'+ (this.offset?0.3:0) + ')';
    };

    Drawer.prototype.toch2mouse = function(event) {
        if(event.touches && event.touches.length){
            event.clientX = event.touches[0].clientX;
            event.clientY = event.touches[0].clientY;
        }
    };

    Drawer.prototype.setEvents = function(value) {
        var action = (value?'add':'remove')+'EventListener';
        document[action]('mousemove', this.pointermove.bind(this));
        document[action]('mouseup', this.pointerup.bind(this));
        document[action]('touchmove', this.pointermove.bind(this));
        document[action]('touchend', this.pointerup.bind(this));
    };

    Drawer.prototype.pointerdown = function(event) {
        // console.log(event);
        this.toch2mouse(event);
        this.drag = this.offset - event.clientX;
        this.domElement.classList.add('move');
        this.setEvents(true);
    };

    Drawer.prototype.pointermove = function(event) {
        this.toch2mouse(event);
        if(this.drag !== false){
            this.set(Math.min(this.drag + event.clientX, this.width));
            event.preventDefault()

            if(this.previousEvents&&event.clientX != this.previousEvents.clientX)this.alpha = event.clientX - this.previousEvents.clientX
            this.previousEvents = event;
        }
    };

    Drawer.prototype.pointerup = function(event) {
        this.toch2mouse(event);
        this.drag = false;
        this.domElement.classList.remove('move');
        this.setEvents(false);
        this.offset = (this.offset > this.width/2) ? this.width  : 0;
        // if(Math.abs(this.alpha) > 8){
        //     this.offset = this.alpha>0 ? this.width  : 0;
        // }
        this.set();
    };

    Drawer.prototype.toggle = function(event) {
        $timeout(function(){
            this.set(this.offset ? 0 : this.width);
        }.bind(this), 500);
    };

    Drawer.prototype.open = function(event) {
        this.set(this.width);
    };

    Drawer.prototype.close = function(event) {
        this.set(this.offset = 0);
    };

    Drawer.prototype.resize = function(){
        main.style.height = window.innerHeight - header.clientHeight + 'px';
    };
    return Drawer;
}]);