angular.module('news', [])
    .controller('newsCtrl', ['$rootScope',  '$scope', '$state', '$timeout', 'db', 'unixDate', '$mdDialog',
        function ($rootScope, $scope, $state, $timeout, db, unixDate, $mdDialog) {
            $rootScope.unixTimeStamp = unixDate.getNowStamp();
            $rootScope.filterOrg = false;
            $scope.currentTime = unixDate.toHumanFormat(unixDate.getNowStamp());
            $scope.mesCount = {};

            $scope.webKey = localStorage.webKey;
            $scope.name = localStorage.name?localStorage.name:'';

            var _viewed_news = localStorage.viewed_news ? JSON.parse(localStorage.viewed_news) : [];

            //var allItems = $scope.newsItems;
            // –Ω–µ —Ç—Ä–æ–≥–∞—Ç—å
            function DialogController($scope, $mdDialog) {
                $scope.hide = function () {
                    $mdDialog.hide();
                };

                $scope.cancel = function () {
                    $mdDialog.cancel();
                };

                $scope.hStyle={
                    "color":"#fff"
                }

                $scope.changeClass=function (inp1,inp2) {
                    if(inp1!=''&&inp2!=''){
                        $scope.hStyle={
                            "color":"#fff"
                        }
                    }
                }

                $scope.addPhoto = function (mode) {
                    //$scope.fieldImg = 'files/hotelmariott.jpg';
                    var options = {
                        size: 640,
                        quality: 75
                    };
                };

                $scope.deletePhoto = function (item) {
                    $scope.fieldImg = null;
                };

                var toHumanFormat = function(unixTimeStamp) {
                    var a = new Date(unixTimeStamp * 1000);

                    //var months = ['–Ø–Ω–≤–∞—Ä—è','–§–µ–≤—Ä–∞–ª—è','–ú–∞—Ä—Ç–∞','–ê–ø—Ä–µ–ª—è','–ú–∞—è','–ò—é–Ω—è','–ò—é–ª—è','–ê–≤–≥—É—Å—Ç–∞','–°–µ–Ω—Ç—è–±—Ä—è','–û–∫—Ç—è–±—Ä—è','–ù–æ—è–±—Ä—è','–î–µ–∫–∞–±—Ä—è'];
                    var months = ['Jan','Feb','Mar','Apr','May','Jun','Jul','Aug','Sep','Oct','Nov','Dec'];
                    var year = a.getFullYear();

                    var month = months[a.getMonth()];
                    //var month = a.getMonth() + 1;
                    if (month < 10) month = '0' + month;

                    var day = a.getDate();

                    var hour = a.getHours();
                    if (hour < 10) hour = '0' + hour;

                    var min = a.getMinutes();
                    if (min < 10) min = '0' + min;

                    var sec = a.getSeconds();
                    if (sec < 10) sec = '0' + sec;

                    //var time = day + '.' + month + '.' + year + ' ' + hour + ':' + min + ':' + sec ;
                    //var time = year + '-' + month + '-' + day + ' ' + hour + ':' + min + ':' + sec ;
                    var time = day + ' ' + month+', '+hour+':'+min;

                    return time;
                };

                $scope.answer = function (answer, fieldTitle, fieldBody, fieldImg) {
                    $("#main").css({"overflow":"auto"});
                    //if(answer=='send'&&(!fieldTitle || !fieldBody)) {
                    if(answer=='send' && !fieldTitle) {
                        //alert('–í—Å–µ –ø–æ–ª—è –¥–æ–ª–∂–Ω—ã –±—ã—Ç—å –∑–∞–ø–æ–ª–Ω–µ–Ω—ã');
                        $scope.hStyle={
                            "color":"red"
                        }
                        //showMessage.show("–í—Å–µ –ø–æ–ª—è –¥–æ–ª–∂–Ω—ã –±—ã—Ç—å –∑–∞–ø–æ–ª–Ω–µ–Ω—ã", 2000);
                        return;
                    }
                    //console.log(fieldTitle, fieldBody);
                    $mdDialog.hide(answer);
                    if(answer=='send'){
                        var d = new Date();
                        d.setHours(d.getHours());
                        var unix_time_stamp = Math.floor(d.getTime() / 1000);
                        //alert(d);
                        //alert(d.toISOString());
                        var new_item = {};
                        new_item['date_public']=unix_time_stamp;
                        new_item['title']=fieldTitle;
                        new_item['desc']=fieldBody;
                        if (fieldImg)
                            new_item['img']=fieldImg;
                        if(localStorage.name){new_item['author']=localStorage.name;}
                        if(!$rootScope.vars.dev && new_item['title'] && new_item['title']!='')db.add('module_notification',new_item);
                        new_item['date_public_human'] = toHumanFormat(new_item['date_public']);
                        $rootScope.unixTimeStamp = Math.floor(d.getTime() / 1000);
                        //alert(toHumanFormat($rootScope.unixTimeStamp));
                        //$scope.newsItems.push(new_item);
                        $rootScope.newsItems.push(new_item);
                        $rootScope.scrollTop();
                        //alert(JSON.stringify(new_item));
                        //alert(JSON.stringify($rootScope.newsItems));
                    }
                };
            }

            // –º–æ–¥—É–ª—å–Ω–æ–µ –æ–∫–Ω–æ
            $rootScope.showAdvanced = function (ev) {
                $("#main").css({"overflow":"hidden"});
                //document.querySelector('main').classList.add(disableScroll);

                $mdDialog.show({
                    controller: DialogController,
                    template: '<md-dialog class="add-news-feed-item-dialog" aria-label="–°–æ–∑–¥–∞—Ç—å –Ω–æ–≤–æ—Å—Ç—å">\
                                    <form>\
                                        <md-toolbar>\
                                            <div class="md-toolbar-tools" style="background: #ed1d24;">\
                                                <h2>Post news</h2>\
                                            </div>\
                                        </md-toolbar>\
                                        <md-dialog-content class="md-dialog-content" style="max-width:800px;max-height:810px;">\
                                            <div style="color: red;font-size: 0.8em;line-height: 1.1em;margin-bottom: 5px;" ng-style="hStyle">Please fill the title field</div>\
                                            <input ng-change="changeClass(fieldTitle,fieldBody)" placeholder="Title" class="dialog-field new-news-title" id="fieldTitle" type="text" ng-model="fieldTitle">\
                                            <textarea ng-change="changeClass(fieldTitle,fieldBody)" placeholder="News content" class="dialog-field new-news-body" id="fieldBody" ng-model="fieldBody"></textarea>\
                                            <div class="photos" ng-show="fieldImg">\
                                                <div class="photo-block">\
                                                    <div>\
                                                        <a href="{{fieldImg | file}}" data-fancybox style="{{ fieldImg | file:\'cover\' }}"></a>\
                                                        <p ng-click="deletePhoto(fieldImg)"><img src="../img/delete.svg" alt=""></p>\
                                                    </div>\
                                                </div>\
                                            </div>\
                                            <div class="buttons">\
                                                <button class="photo_ photo_album" ng-disabled="fieldImg" ng-click="addPhoto(\'gallery\')"><img src="../img/photo-album.svg"> <span>\Upload a photo\</span></button>\
                                                <button class="photo_ photo_camera" ng-disabled="fieldImg" ng-click="addPhoto()"><img src="../img/photo-camera.svg"> <span>\Make a photo\</span></button>\
                                            </div>\
                                        </md-dialog-content>\
                                        <md-dialog-actions layout="row">\
                                            <span flex></span>\
                                            <md-button ng-click="answer(\'cancel\')" style="padding:0 5px; background:#e6e6e6">\Cancel\</md-button>\
                                            <md-button ng-click="answer(\'send\', fieldTitle, fieldBody,fieldImg)" style="padding:0 5px; background:#d8eae4">\Post\</md-dialog-actions>\
                                    </form>\
                                </md-dialog>',
                    parent: angular.element(document.body),
                    targetEvent: ev,
                    clickOutsideToClose: true,
                    fullscreen: $scope.customFullscreen // Only for -xs, -sm breakpoints.
                })
                    .then(function (answer) {
                        if(answer == 'send') {
                        } else if(answer == 'cancel') {
                            console.log('canceled');
                        }
                    }, function () {
                        console.log('cancelled');
                    });
            };

            $scope.checkLike = function (id) {
                var flag = false;
                var name = localStorage.name?localStorage.name:'';

                for (var i = 0, len = $rootScope.newsItems.length; i < len; i++) {
                    if ($rootScope.newsItems[i].guid === id) {
                        if ($rootScope.newsItems[i].like)
                            checkName($rootScope.newsItems[i].like);
                        break;
                    }
                }

                function checkName(names) {
                    for (var i = 0, len = names.length; i < len; i++) {
                        if (names[i] === name) {
                            flag = true;
                            break;
                        }
                    }
                }

                return flag;
            };

            $scope.like = function (id) {
                //console.log(id);
                var name = localStorage.name?localStorage.name:'';

                var obj = {};

                if ($scope.checkLike(id) === false) {
                    for (var i = 0, len_i = $rootScope.newsItems.length; i < len_i; i++) {

                        if ($rootScope.newsItems[i].guid === id) {
                            if (!$rootScope.newsItems[i].like)
                                $rootScope.newsItems[i].like = [];

                            $rootScope.newsItems[i].like.push(name);
                            obj[id] = {like: '+' + name};
                            break;
                        }
                    }
                } else {
                    for (var k = 0, len_k = $rootScope.newsItems.length; k < len_k; k++) {
                        if ($rootScope.newsItems[k].guid === id) {
                            var index = $rootScope.newsItems[k].like.indexOf(name);
                            $rootScope.newsItems[k].like.splice(index, 1);
                            obj[id] = {like: '-' + name};
                            break;
                        }
                    }
                }
            };

            $scope.deleteNews = function (id) {
                console.log(id);

                var obj = {};
                obj[id] = { status: 'deleted' };
                for (var k = 0, len_k = $rootScope.newsItems.length; k < len_k; k++) {
                    if ($rootScope.newsItems[k].guid === id) {
                        $scope.$applyAsync(function () {
                            $rootScope.newsItems.splice(k, 1);
                        });
                        break;
                    }
                }

            };
            $scope.showComments = function (id,index) {
                //$rootScope.scrollTop();
                $scope.curNewsItemId = id;
                $scope.curLocalNewsItemId = index;
                //alert($scope.curNewsItemId);
                //$("#main").css({"overflow":"hidden"});
                $scope.messages = [];
                $rootScope.showCommentsBlock = id;
                var arr = [];
                $scope.messageLimit = 20;
            };

            $scope.message = {};

            $scope.sendMessage = function () {
                if ($scope.message.id !== undefined) {
                    for (var i = 0, len = $scope.messages.length; i < len; i++) {
                        if ($scope.messages[i].id === $scope.message.id) {
                            $scope.messages[i].text = $scope.message.text;
                        }
                    }
                } else {
                    var obj = {};
                    var key = createID();
                    obj[key] = {
                        author: localStorage.name?localStorage.name:'',
                        date: new Date().getTime(),
                        text: $scope.message.text,
                        id: key
                    };
                    $scope.messages = [obj[key]].concat($scope.messages);
                }
                //=========–ø–ª—é—Å—É–µ–º –∫–æ–º–µ–Ω—Ç—ã=====================
                //alert($scope.curNewsItemId);
                db.edit('module_notification', {comcount: 'math:+1'}, $scope.curNewsItemId);
                if($rootScope.newsItems[$scope.curLocalNewsItemId].comcount)$rootScope.newsItems[$scope.curLocalNewsItemId].comcount=+$rootScope.newsItems[$scope.curLocalNewsItemId].comcount+1;
                else $rootScope.newsItems[$scope.curLocalNewsItemId].comcount = 1;
                //=============================================
                $scope.message = {};
                delete $scope.cancelEdit;
            };

            function createID() {
                return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
                    var r = Math.random() * 16 | 0, v = c === 'x' ? r : (r & 0x3 | 0x8);
                    return v.toString(16);
                })
            }

            $rootScope.header.back = function () {
                if ($rootScope.showCommentsBlock !== undefined){
                    delete $rootScope.showCommentsBlock;
                    //$("#main").css({"overflow":"auto"});
                }
                else
                    location.hash = '#/main';
            };

            $scope.loadMoreMessages = function () {
                if ($scope.messages.length > $scope.messageLimit) {
                    $scope.messageLimit += 20;
                }
            };

            // –ø—Ä–æ–≤–µ—Ä–∫–∞ –≤–∏–¥–∏–º–æ—Å—Ç–∏ –≤ –∑–∞–≤–∏—Å–∏–º–æ—Å—Ç–∏ –æ—Ç –Ω–∞–ª–∏—á–∏—è –≥–∞–ª–æ—á–∫–∏
            $rootScope.checkVisibility = function (orgMark) {
                var result;

                if (orgMark && $rootScope.filterOrg) {                      // –µ—Å–ª–∏ –µ—Å—Ç—å —É –Ω–æ–≤–æ—Å—Ç–∏ –µ—Å—Ç—å –ø—Ä–∏–∑–Ω–∞–∫ –æ—Ä–≥–∞–Ω–∏–∑–∞—Ç–æ—Ä–∞ –∏ –≥–∞–ª–æ—á–∫–∞ –æ—Ç–º–µ—á–µ–Ω–∞
                    result = true;
                } else if ((!orgMark && $rootScope.filterOrg)) {             // –µ—Å–ª–∏ –µ—Å—Ç—å —É –Ω–æ–≤–æ—Å—Ç–∏ –ù–ï–¢ –ø—Ä–∏–∑–Ω–∞–∫–∞ –æ—Ä–≥–∞–Ω–∏–∑–∞—Ç–æ—Ä–∞ –∏ –≥–∞–ª–æ—á–∫–∞ –æ—Ç–º–µ—á–µ–Ω–∞
                    result = false;
                } else if ((orgMark && !$rootScope.filterOrg)) {             // –µ—Å–ª–∏ –µ—Å—Ç—å —É –Ω–æ–≤–æ—Å—Ç–∏ –µ—Å—Ç—å –ø—Ä–∏–∑–Ω–∞–∫ –æ—Ä–≥–∞–Ω–∏–∑–∞—Ç–æ—Ä–∞ –∏ –≥–∞–ª–æ—á–∫–∞ –ù–ï –æ—Ç–º–µ—á–µ–Ω–∞
                    result = true;
                } else if ((!orgMark && !$rootScope.filterOrg)) {             // –µ—Å–ª–∏ –µ—Å—Ç—å —É –Ω–æ–≤–æ—Å—Ç–∏ –ù–ï–¢ –ø—Ä–∏–∑–Ω–∞–∫–∞ –æ—Ä–≥–∞–Ω–∏–∑–∞—Ç–æ—Ä–∞ –∏ –≥–∞–ª–æ—á–∫–∞ –ù–ï –æ—Ç–º–µ—á–µ–Ω–∞
                    result = true;
                }

                return result;
            };

            // –ø–æ–ª—É—á–∞–µ–º –¥–∞–Ω–Ω—ã–µ –¥–ª—è –ª–µ–Ω—Ç—ã –Ω–æ–≤–æ—Å—Ç–µ–π
            $scope.getNewsItems = function () {
                db.get('module_notification',!$rootScope.vars.dev).then(function (resp) {
                    $scope.loading = true;
                    var _data = [], obj = {};

                    for (var i in resp.values) {
                        obj = {};
                        obj = resp.values[i];
                        obj['guid'] = i;
                        if(obj['comcount']){obj['comcount']=+obj['comcount'];}
                        else obj['comcount']=0;
                        if(obj['date_public']) { obj['date_public_human'] = unixDate.toHumanFormat(obj['date_public']); }
                        if(obj['date_public']) { obj['date_public'] = +obj['date_public']; }
                        _data.push(obj);

                        if (_viewed_news.indexOf(i) === -1) {
                            _viewed_news.push(i);
                        }
                    }
                    ;

                    $rootScope.newsItems = _data;
                    for(var i=0, len = $rootScope.newsItems.length; i<len; i++){
                        $rootScope.newsItems[i]['ind'] = i;
                    }
                    //console.log('$scope.newsItems', $scope.newsItems);
                    $scope.loading = false;
                    localStorage.viewed_news = JSON.stringify(_viewed_news);
                    //alert(JSON.stringify($scope.newsItems));
                });
            };

            $scope.getNewsItems();
        }
    ])
    .directive("scrollable", function () {
    return {
        restrict: 'A',
        link: function (scope, element, attrs) {
            var container = angular.element(element);
            container.bind("scroll", function (evt) {
                if (container[0].offsetHeight + container[0].scrollTop + 20 >= container[0].scrollHeight) {
                    scope.$apply(attrs.scrollable);
                }
            });
        }
    };
});