angular.module('premod', [])
    .controller('premodCtrl', ['$scope', '$rootScope', 'db', '$timeout', '$state', 'file', '$mdDialog', 'communication', '$stateParams', '$http','$location',
        function($scope, $rootScope, db, $timeout, $state, file, $mdDialog, communication, $stateParams, $http,$location) {

            $rootScope.header.moderation=false;
            var timer;
            $scope.$on('$destroy', function () {
                $timeout.cancel(timer);
            });

            //$rootScope.testmode='yes';
            //======–û–ø—Ä–µ–¥–µ–ª—è–µ–º testmode (–µ—Å–ª–∏ evp –∏–ª–∏ –æ—Ä–≥–∞–Ω–∏–∑–∞—Ç–æ—Ä - –¥–æ—Å—Ç—É–ø–Ω–∞ –º–æ–¥–µ—Ä–∞—Ü–∏—è)==========
            var my_id = localStorage.my_id?localStorage.my_id:false;
            if(my_id){
                db.get('participants',!$rootScope.vars.dev).then(function (participants) {
                    if(participants.values[my_id].status==2 || participants.values[my_id].status==3){
                        $rootScope.testmode='yes';
                    }
                    else{
                        $state.go('speakers');
                    }
                });
            }
            //===================================================================================

            $scope.$parent.header.back = function(){
                    $state.go('speakers');
            };

            $scope.showQuestions=false;

            $scope.getTables=function(){
                //alert('getTable');
                db.get('speakers', !$rootScope.vars.dev).then(function(data) {
                    var speakers=[];
                    for(var v in data.values){
                        data.values[v]['order'] = data.values[v]['order']?+data.values[v]['order']:1000;
                        speakers.push(data.values[v]);
                    }
                    $scope.speakers=speakers;
                    db.get('questions',!$rootScope.vars.dev).then(function(data2) {
                        for(var s in $scope.speakers){
                            var questions_number=0;
                            var q_count=0;
                            for(var v in data2.values){
                                if(data2.values[v].speaker_email==$scope.speakers[s].email){
                                    questions_number++;
                                }
                                q_count++;
                            }
                            var viewed=localStorage[$scope.speakers[s].email]?localStorage[$scope.speakers[s].email]:0;
                            $scope.speakers[s]['push_count']=questions_number - +viewed;
                        }
                    }, function(resp) {
                        //console.log(111);
                    });
                });

                timer = $timeout(function () {
                    $scope.getTables();
                }, 7000);

            }

            $scope.getTables();

            $scope.openSpeaker=function(speaker){
                $scope.currentSpeaker=speaker;
                $scope.$parent.header.title=speaker.first_name+" "+speaker.last_name;
                db.get('questions',!$rootScope.vars.dev).then(function(data) {
                    $scope.loading = true;
                    var questions=[];
                    for(var v in data.values){
                        if(data.values[v].speaker_email==speaker.email){
                           data.values[v]['id']=v;
                           questions.push(data.values[v]);
                        }

                    }
                    $scope.questions=questions;
                    localStorage[speaker.email]=$scope.questions.length;
                    $scope.loading = false;
                    $scope.showQuestions=true;
                });

                $scope.$parent.header.back = function(){
                    if($scope.showQuestions){
                        //console.log('gdfgdfgotosp');
                        $scope.showQuestions=false;
                        $scope.$parent.header.title="–ü—Ä–µ–º–æ–¥–µ—Ä–∞—Ü–∏—è –≤–æ–ø—Ä–æ—Å–æ–≤";
                        $scope.getTables();
                    }
                    else {
                        //console.log('gotosp');
                        $state.go('speakers');
                    }
                };

            }

            $scope.premodToggle=function(question){
                if(question.moderation==1){
                    question.moderation=0+'';
                }
                else {
                    question.moderation=1+'';
                }
                //console.log(question);
                //console.log(question.id);
                db.edit('questions', question, question.id);
            }
        }
        ]);