/* Layout height */
(function(){
  if(!/ipad|iphone/gi.test(window.navigator.userAgent)){
    setInterval(function(){
      var e =  document.querySelectorAll('main, md-tabs:not(.md-no-tab-content):not(.md-dynamic-height)');
      for(var i=0; i<e.length; i++)e[i].style.height = window.innerHeight-56+'px'
    }, 100);
  } else {
        var header = document.querySelector('header');
        header.classList.add('ios');
        
        var nav = document.querySelector('nav');
        nav.classList.add('ios');
        
        var main = document.querySelector('main');
        main.classList.add('ios');

        var newsFeedBlock = document.querySelector('newsFeedBlock');
        newsFeedBlock.classList.add('ios');
    }
}());
