angular.module('main', [])
    .controller('mainCtrl', ['$rootScope', '$scope', '$state', '$timeout', 'db', 'unixDate', '$cookies',
        function ($rootScope, $scope, $state, $timeout, db, unixDate, $cookies) {
            $scope.items = pageOptions.slice(2);
            $scope.$parent.header.hide = true;
            $scope.CurrUserEmail = $rootScope.vars.contacts_email?$rootScope.vars.contacts_email.trim():'';
            $scope.unixTimeStamp = unixDate.getNowStamp();

            $scope.animate=localStorage.animate==0?false:true;
            $scope.welcome = localStorage.welcome?true:false;

            $scope.photo='1601bfe1-880a-4691-b4af-89793ccc7d07.jpg';

            $scope.continue = function () {
                $timeout(function () {
                    $scope.welcome = true;
                    localStorage.welcome = 1;
                },500);
            }
            
            if(!$scope.welcome){
                db.get('agreement').then(function (data) {
                    for(var v in data.values){
                        $scope.agreement = data.values[v].text;
                        break;
                    }
                })
            }

            if(!$scope.animate){
                localStorage.animate=1;
            }

            $scope.goTo=function (page) {
                $timeout(function () {
                    $state.go(page);
                },500);
            };

            var getMyData = function (id) {
                db.get('participants',!$rootScope.vars.dev).then(function (data) {
                    var me = data.values[id];
                    if(me.name && me.lname){$scope.name = me.name+' '+me.lname;}
                    $scope.photo = me.photo?me.photo:false;
                    localStorage.name=$scope.name;
                });
            }

/*            if (navigator.userAgent.indexOf ('Mac')!= -1){
                var my_id = $cookies.get('my_id')?$cookies.get('my_id'):false;
                var ios = true;
            }
            else{
                var my_id = localStorage.my_id?localStorage.my_id:false;
                var ios = true;
            }*/

            //alert('all cookies'+JSON.stringify($cookies.getAll()));

            var my_id = localStorage.my_id?localStorage.my_id:'';
            $scope.name=localStorage.name?localStorage.name:'';

            // –ø–æ–ª—É—á–∞–µ–º –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –Ω–µ–ø—Ä–æ—á–∏—Ç–∞–Ω–Ω—ã—Ö –ø—É—à–µ–π
            $scope.getUnreadPushCnt = function() {
                var _push_count = (localStorage["push_count"]) ? +localStorage["push_count"] : 0;    // –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –ø—Ä–æ—Å–º–æ—Ç—Ä–µ–Ω–Ω—ã—Ö —É–≤–µ–¥–æ–º–ª–µ–Ω–∏–π
                //var _push_count = ($cookies.get('push_count')) ? +$cookies.get('push_count') : 0;    // –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –ø—Ä–æ—Å–º–æ—Ç—Ä–µ–Ω–Ω—ã—Ö —É–≤–µ–¥–æ–º–ª–µ–Ω–∏–π
                var unixTimeStamp = unixDate.getNowStamp();

                db.get('notices').then(function (resp) {
                    //console.log(resp);
                    var push_count = 0;

                    for (var i in resp.values) {
                        if( resp.values[i]['date_public'] < $scope.unixTimeStamp &&
                            (resp.values[i]['users'].indexOf($scope.CurrUserEmail) != -1 ||
                                resp.values[i]['users'].length == 0)) {
                                    push_count += 1;        // –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ —É–≤–µ–¥–æ–º–ª–µ–Ω–∏–π —Ç–µ–∫—É—â–µ–≥–æ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è
                        }
                    }

                    $scope.push_count = push_count - _push_count;      // –º–æ–∏ –º–∏–Ω—É—Å –ø—Ä–æ—Å–º–æ—Ç—Ä–µ–Ω–Ω–Ω—ã–µ
                    if(!$scope.$$phase) { $scope.$apply(); }
                });
            };
            $scope.getUnreadPushCnt();


            //–ø–æ–ª—É—á–∞–µ–º –∫–æ–ª-–≤–æ –Ω–µ–ø—Ä–æ—á–∏—Ç–∞–Ω–Ω—ã—Ö –Ω–æ–≤–æ—Å—Ç–µ–π
            var _viewed_news = (localStorage.viewed_news) ? JSON.parse(localStorage.viewed_news) : [];
            db.get('module_notification',!$rootScope.vars.dev).then(function (data) {
                var _news_count = 0;

                for (var v in data.values) {
                    //console.log(data.values[v]);

                    if (_viewed_news.indexOf(v) === -1 && data.values[v].hasOwnProperty('title'))
                        _news_count++;
                }

                $scope.news_count = _news_count;
            });
        }
    ]);
