angular.module('agenda', ['angular-timeline'])
    .controller('agendaCtrl', ['$rootScope', '$scope', '$state', 'db', '$timeout', '$stateParams', '$sce',
        function ($rootScope, $scope, $state, db, $timeout, $stateParams, $sce) {
            $scope.items = pageOptions.slice(1);
            $scope.$parent.header.hide = false;
            //console.log($stateParams);

            $scope.trustAsHtml = function(string) {
                return $sce.trustAsHtml(string);
            };

            $rootScope.header.agenda2 = true;
            if($rootScope.vars['personal_team'] && $rootScope.vars['personal_team']=='agenda2'){
                $rootScope.agenda2 = 'yes';
            }

            var loadAgenda=function (ref) {
                db.get(ref).then(function (agenda) {
                    //console.log(agenda);
                    $scope.loading=false;
                    db.get('speakers',!$rootScope.vars.dev).then(function (data) {
                        $scope.speakers = data.values;
                        //console.log($scope.speakers);
                    });

                    $timeout(function () {
                        var date = new Date();
                        $scope.today = $stateParams.tab ? $stateParams.tab : agenda.headers.date.options.indexOf([('0'+date.getDate()).slice(-2), ('0'+(date.getMonth()+1)).slice(-2), date.getFullYear()].join('.'));
                        if ($scope.today === -1)
                            $scope.today = 0;
                        if ($stateParams.tab)
                            $scope.today = $stateParams.tab;

                        document.querySelectorAll('.md-tab')[+$scope.today].click();
                    }, 200);


                    var _data = [], obj = {};
                    var r = new RegExp("\x22+","g"); // –≤–æ–æ–±—â–µ, –≤ —à–∞–±–ª–æ–Ω–µ –º–æ–∂–Ω–æ —É–∫–∞–∑–∞—Ç—å –ª—é–±—É—é —Å—Ç—Ä–æ–∫—É

                    for (var i in agenda.values) {
                        obj = {};
                        obj = agenda.values[i];
                        obj['guid'] = i;

                        if (obj.hasOwnProperty('order'))
                            obj['order'] = +obj['order'];
                        else obj['order'] = 10000;

                        if (obj.hasOwnProperty('description'))
                            obj['description'] = obj['description'].replace(r, "'");

                        _data.push(obj);
                    }

                    $scope.headers = agenda.headers;
                    $scope.events = _data;
                    //console.log($scope.headers);

                    if ($stateParams.id && $stateParams.event) {
                        $timeout( function() {
                            $('.md-active').animate({
                                scrollTop: $("#user-" + $stateParams.id + '_' + $stateParams.event).offset().top - 113
                            }, 500);

                        }, 1000);
                    }

                }, function () {
                    //console.log('–ù–µ –Ω–∞–π–¥–µ–Ω .json');
                });
            };

            $scope.loading=true;
            loadAgenda('agenda');

            var isAgenda2 = false;
            $rootScope.showAgenda2 = function () {
                $scope.loading=true;
                $rootScope.header.agenda2 = false;
                $rootScope.header.title = '–î–æ–ø. –ø—Ä–æ–≥—Ä–∞–º–º–∞';
                isAgenda2 = true;
                loadAgenda('agenda2');
            }

            $scope.$parent.header.back = function(){
                if(isAgenda2){
                    isAgenda2=false;
                    $scope.loading=true;
                    loadAgenda('agenda');
                    $rootScope.header.title = '–ü—Ä–æ–≥—Ä–∞–º–º–∞';
                    $rootScope.header.agenda2 = true;
                }
                else {
                    location.hash = '#/main';
                }
            };


            //–°–≤–∞–π–ø –ø–æ —Ç–∞–±–∞–º
            $scope.swipe = function (ev, turn) {
                var _tabs = document.querySelectorAll('md-tab-item');

                if (turn === 'right') {
                    if ($scope.today < _tabs.length - 1) $scope.today += 1
                } else {
                    if ($scope.today > 0) {
                        $scope.today -= 1
                    }
                }
                $timeout(function () {
                    _tabs[$scope.today].click()
                })
            };

            $scope.currentTab = function (tab) {
                $scope.today = tab;
            }

            $scope.openSpeaker = function(id, f_eventguid) {
                //console.log(id);
                //console.log($scope.today);
                //console.log(f_eventguid);
                $state.go('speakers', {'id': id, tab: $scope.today});
            };


        }
    ]);

