angular.module('info', [])
    .controller('infoCtrl', ['$rootScope', '$scope', '$state', '$timeout','db','$http', '$stateParams', '$mdDialog',
        function ($rootScope, $scope, $state, $timeout,db,$http,$stateParams, $mdDialog) {
            //$scope.section = 'hotel';
                $scope.curSector=-1;
                $rootScope.header.back = function(){
                        $scope.curSector=-1;
                        if($scope.section){
                            $rootScope.header.simpleSearch = false;
                            if($scope.section!='hotel_photos' && $scope.section!='hotel_halls')    {
                                    if($scope.currentGuest){
                                            $scope.currentGuest = false ;
                                    }
                                    else{
                                            $scope.section=false;
                                            $rootScope.header.title = 'Useful Information';
                                    }
                            }
                            else{
                                    $scope.section='hotel';
                                    $scope.openSection('hotel','Marriott Minsk');
                            }
                        }
                        else {
                                localStorage.animate=0;
                                location.hash = '#/main';
                        }
                };

                $scope.showGuest=function(guest){
                        $scope.currentGuest = guest;
                }

                $scope.sendFreetime = function(obj){
                    //console.log(obj);

                    var data = obj;
                    /*for(var key in obj)
                        data[key] = obj[key];*/

                    //alert(JSON.stringify(data));

                    db.add('reetime_list',data);
                    //===========BE–ë –ó–ê–ü–ò–°–¨ –í –¢–ê–ë–õ–ò–¶–£==============

                    //=============================================
                    $mdDialog.show(
                        $mdDialog.alert()
                            .title('–î–∞–Ω–Ω—ã–µ –æ—Ç–ø—Ä–∞–≤–ª–µ–Ω—ã')
                            .ok('–û–∫')
                    );

                    //angular.element('md-input-container' + currentContact.id).val('');
                    //<md-input-container class="inp md-input-invalid"

                    /*for (var j in obj)
                        obj[j] = '';*/
                }

                $scope.openSection = function (section,title) {
                      $scope.loading=true;
                      $timeout(function () {
                              $scope.section = section;
                              $rootScope.header.title = title;
                              //=======—Å—Ö–µ–º–∞ —Ä–∞—Å—Å–∞–¥–∫–∏===============
                              if (section == 'business_scheme') {
                                      $scope.opened_table = [];
                                      $rootScope.header.simpleSearch = true;
                                      if ($rootScope.search) $rootScope.search = '';
                              }
                              //====================================
                              db.get(section).then(function (data) {
                                      $scope.loading=true;
                                      var items = [];
                                      for(var v in data.values){
                                              var item = {};
                                              item = data.values[v];
                                              if(item['seat'])item['seat']=+item['seat'];
                                              item['id'] = v;
                                              item['order'] =  data.values[v]['order']?+data.values[v]['order']:1000;
                                              items.push(item);
                                      }
                                      $scope.items = items;
                                      $scope.headers = data.headers;
                                      $scope.loading=false;
                                      if($stateParams.section=='guests'){
                                              for(var i=0; i<($scope.items).length; i++){
                                                      if($scope.items[i].lname==$stateParams.lname){
                                                              $scope.showGuest($scope.items[i]);
                                                      }
                                              }
                                      }
                              },function(error){
                                      $scope.loading=false;
                              });
                      },500);

                }
                //$scope.openSection($scope.section,'–ò–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è –æ–± –æ—Ç–µ–ª–µ');

                //=======—Å—Ö–µ–º–∞ —Ä–∞—Å—Å–∞–¥–∫–∏===============
                $scope.customFilter = function(ftable) {
                        return function(item) {
                                return ($scope.headers.table.options[item.table] == ftable);
                        }
                }

                $scope.toggleVisible = function(find) {
                        if ($scope.opened_table.indexOf(find) > -1)
                                $scope.opened_table.splice($scope.opened_table.indexOf(find),1);
                        else
                                $scope.opened_table.push(find);
                }

                $scope.opened_table = [];

                //=====================================

                if ($stateParams.section){
                        if($stateParams.section=='business_scheme'){
                            $scope.openSection($stateParams.section,'Seating chart');
                        }
                        else if($stateParams.section!='guests'&&$stateParams.section!='main'){
                            $scope.openSection($stateParams.section,'Organizers');
                        }
                        else{
                            if($stateParams.section!='main')$scope.openSection($stateParams.section,'Guests of the Conference');
                        }
                }


            //===================–ü–û–ì–û–î–ê===================================
            $scope.APPID = "4ccea7fb89b8c08e7e7ccb3e44a1c0ee"; // API –∫–ª—é—á openweathermap

                //$scope.Wlatitude=43.35;
                // $scope.Wlongitude =39.43;

                $scope.openOrg = function(f_id) {
                        //console.log(f_id);
                        $state.go('questions_to_orgs', {id: f_id});
                };

                $scope.locationSuccess = function(lat,lon) {
                        var geoApi = "http://api.openweathermap.org/data/2.5/forecast";
                        $http({
                                method: 'GET',
                                headers: {
                                    'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8;'
                                },
                                url: geoApi,
                                params: {
                                        lat: lat,
                                        lon: lon,
                                        lang: 'en',
                                        appid: $scope.APPID,
                                        units: 'metric',
                                        cnt: 38
                                },
                                responseType: 'json',
                                cache: false
                        }).then(function successCallback(response) {
                                //alert(JSON.stringify(response))
                                // this callback will be called asynchronously
                                // when the response is available
                                //console.log("locationSuccess");
                                var d = response.data;
                                //console.log(d);
                                if (d) {
                                        var data = d["list"][0]; // —Ç–µ–∫—É—â–∏–µ –¥–∞–Ω–Ω—ã–µ –æ –ø–æ–≥–æ–¥–µ
                                        //console.log(d);
                                        var description = data["weather"][0];
                                        var icon = description["icon"];
                                        var temp = data["main"]["temp"];
                                        var info = description["description"];
                                        var wind = data["wind"]["speed"];
                                        var iconName = getLetter(icon);
                                        $scope.icon = "../img/weather/" + iconName + ".svg";
                                        $scope.temp = Math.round(temp) + "¬∞C";
                                        $scope.info = info;
                                        $scope.wind =wind;
                                        localStorage.icon=$scope.icon;
                                        localStorage.temp=$scope.temp;
                                        localStorage.info=$scope.info;
                                        localStorage.wind=$scope.wind;
                                }

                        }, function errorCallback(response) {
                                $scope.icon=localStorage.icon;
                                $scope.temp=localStorage.temp;
                                $scope.info=localStorage.info;
                                $scope.wind=localStorage.wind;
                        });

                }

                if($rootScope.vars.dev){
                        $scope.locationSuccess(48.2,16.4);
                        //$scope.locationSuccess(32,34);
                }
                $scope.locationSuccess(48.2,16.4);

                $scope.updateWeather=function(){
                }

                function getLetter(icon) {
                        var letter = "";
                        if (icon == "01d" || icon == "01n") {
                                letter = "B";
                        } else if (icon == "02d" || icon == "02n") {
                                letter = "H";
                        } else if (icon == "03d" || icon == "03n") {
                                letter = "H";
                        } else if (icon == "04d" || icon == "04n") {
                                letter = "Y";
                        } else if (icon == "09d" || icon == "09n") {
                                letter = "R";
                        } else if (icon == "10d" || icon == "10n") {
                                letter = "Q";
                        } else if (icon == "11d" || icon == "11n") {
                                letter = "P";
                        } else if (icon == "13d" || icon == "13n") {
                                letter = "W";
                        } else if (icon == "50d" || icon == "50n") {
                                letter = "M";
                        } else {
                                letter = "0S"
                        }
                        return letter;
                }
                //===================END –ü–û–ì–û–î–ê===============================
        }]).filter('search3', function () {
        return function (data, text, people) {
                text = text || '';
                text = text.toLowerCase();
                var out = [];
                //console.log(data);
                //console.log(text);
                //console.log(people);
                if (text.length < 3) return data;

                for (var i = 0; i < people.length; i++) {
                        var flname = (people[i].hasOwnProperty('fio')) ? people[i]['fio'].toLowerCase() : "";

                        if (flname.indexOf(text) > -1 && people[i].hasOwnProperty('table') && out.indexOf(data[people[i]['table']] == -1)) {
                                console.log(people[i]);
                                out.push(data[people[i]['table']]);
                        }
                }
                return out;
        };
});;