angular.module('app.filters', [])
.filter('cutPhone', function($rootScope) {
      return function (phone) {
          if (!phone) return;
          var width = document.documentElement.clientWidth;
          if('ios' == $rootScope.vars.device_platform && width > 760) {
              phone = phone.replace(/\/\?epadvisorIgnoreRequest=true/g, "");
          } else if ('android' == $rootScope.vars.device_platform){
              phone = phone.replace(/\/\?epadvisorIgnoreRequest=true/g, "");
          }
          return phone;
      }
    })
.filter('timeAgo', function() {
  return function(date, lang) {
    var time = (new Date()).getTime() - (date*1000);
    var date = new Date(time - 62167230000000);

    var words = {
      s: ['—Å–µ–∫—É–Ω–¥—É', '—Å–µ–∫—É–Ω–¥—ã', '—Å–µ–∫—É–Ω–¥',  '—Å–µ–∫—É–Ω–¥—É',  'getSeconds'],
      m: ['–º–∏–Ω—É—Ç—É',  '–º–∏–Ω—É—Ç—ã',  '–º–∏–Ω—É—Ç',   '–º–∏–Ω—É—Ç—É',   'getMinutes'],
      h: ['—á–∞—Å',     '—á–∞—Åa',    '—á–∞—Å–æ–≤',   '—á–∞—Å',      'getHours'],
      d: ['–¥–µ–Ω—å',    '–¥–Ω—è',     '–¥–Ω–µ–π',    '–¥–Ω–µ–π',     'getDate'],
      w: ['–Ω–µ–¥–µ–ª—é',  '–Ω–µ–¥–µ–ª—å',  '–Ω–µ–¥–µ–ª—å',  '–Ω–µ–¥–µ–ª—é',   ''],
      M: ['–º–µ—Å—è—Ü',   '–º–µ—Å—è—Ü–∞',  '–º–µ—Å—è—Ü–æ–≤', '–º–µ—Å—è—Ü',    'getMonths'],
      y: ['–≥–æ–¥',     '–≥–æ–¥–∞',    '–ª–µ—Ç',     '–≥–æ–¥',      'getYear'],
      c: ['–≤–µ–∫',     '–≤–µ–∫–∞',    '–≤–µ–∫–æ–≤',   '–≤–µ–∫',      ''],
    };

    var getNumber = function(value){
      var char = value.toString().slice(-1);
      if(char>=5||char==0) return 2;
      else if(char>=2) return 1;
      else if(char==1) return 0;
    };

    var get–°ollocation = function(word, number){
      return number + ' ' + words[word][getNumber(number)];
    };

    var getFormat = function(value){
      return get–°ollocation(value, date[words[value][4]]());
    };

    var getDate = function(str){
      var arr = [];
      for(var i=0; i<str.length; i++)arr.push(getFormat(str[i]));
      return arr.join(', ') + ' –Ω–∞–∑–∞–¥';
    };

    var result = [];
    if(     time <           10*1000) return '—Ç–æ–ª—å–∫–æ —á—Ç–æ';
    else if(time <           60*1000) return getDate('s');
    else if(time <        60*60*1000) return getDate('m');
    else if(time <     24*60*60*1000) return getDate('h');
    else if(time <  31*24*60*60*1000) return getDate('d');
    else if(time < 365*24*60*60*1000) return getDate('Md');
    else                              return getDate('y');
  };
})

.filter('file', function() {
  return function(file, type) {
   if(!file) return false;

    var storages = {
      /*'ondemand': 'http://epadvisor/ondemand/',*/
      'ondemand': '../files/',
      'static': '../static/',
      'files': '../files/'
    };

    var url = "";
    if (file.split("/")[0] == "ondemand") {
      url = storages['ondemand'] + file.split("/")[1];
    } else if (file.split("/")[0] == "static"){
      url = storages['static'] + file.split("/")[1];
    } else if (file.split("/")[0] == "files") {
      url = storages['files'] + file.split("/")[1];
    } else {
      url = storages['ondemand'] + file;
      //url = file; // –æ—à–∏–±–∫–∞ –≤ –ø—É—Ç–∏ —Ñ–∞–π–ª–∞, –≤–æ–∑–≤—Ä–∞—â–∞–µ–º –∏—Å—Ö–æ–¥–Ω—É—é —Å—Ç—Ä–æ–∫—É
    }

    switch(type) {
      case 'bg': return 'background-image: url('+url+');';
      case 'cover': return 'background: url('+url+') center no-repeat; background-size: cover;';
      case 'contain': return 'background: url('+url+') center no-repeat; background-size: contain;';
      default: return url;
    }
  };
})

.filter("html", ['$sce', function($sce) {
  return function(htmlCode, limit){
    return $sce.trustAsHtml(htmlCode.split(/[\s]+/).slice(0, limit).join(' ') + (htmlCode.length>limit?'‚Ä¶':''));
  }
}])

.filter('search', function() {
  return function(data, text) {
    text = text || '';
    text = text.toLowerCase();
    var out = [];
    if (text.length < 3) return data;
    for (var i = 0; i < data.length; i++) {
      var fname = (data[i].hasOwnProperty("personal_first_name")) ? data[i]["personal_first_name"].toLowerCase() : "";
      var lname = (data[i].hasOwnProperty("personal_last_name")) ? data[i]["personal_last_name"].toLowerCase() : "";
      var flname = fname + " " + lname;
      if (fname.indexOf(text) > -1 ||
          lname.indexOf(text) > -1 ||
          flname.indexOf(text) > -1) {
        out.push(data[i])
      }
    }
    return out;
  };
})
.filter('simpleSearch', function () {
  return function (input, search) {
    if (!input) return input;
    if (!search) return input;
    var expected = ('' + search).toLowerCase();
    var result = {};
    angular.forEach(input, function (value, key) {
      var firstname = ('' + value.name).toLowerCase();
      if (firstname.indexOf(expected) !== -1 )  {
        result[key] = value;
      }
    });
    return result;
  }
});