angular.module('participants', [])
    .controller('participantsCtrl', ['db', '$rootScope', '$scope', '$state', '$timeout', '$mdDialog', 'Users', 'scroll', '$http','$stateParams','$cookies',
        function (db, $rootScope, $scope, $state, $timeout, $mdDialog, Users, scroll, $http,$stateParams,$cookies) {

            $scope.users = new Users();
            $rootScope.header.simpleSearch = true;
            if ($rootScope.search) $rootScope.search = '';

            $scope.gender = localStorage.gender?localStorage.gender:'';

            $rootScope.header.search_reset = function () {
                $rootScope.search = '';
                $scope.users.step = $scope.users.save_step;
            };

            //$scope.my_id = $cookies.get('my_id');
            $scope.my_id = localStorage.my_id?localStorage.my_id:'';
            $scope.mode = $stateParams.mode?$stateParams.mode:0;
            $scope.choosenId = $rootScope['neighbor1']?$rootScope['neighbor1'].split('/')[0]:'none';
            if($scope.choosenId=='none'){
                $scope.choosenId = $rootScope['neighbor2']?$rootScope['neighbor2'].split('/')[0]:'none';
            }

            var backToProfile = function () {
                $rootScope.header.back = function () {
                    if($stateParams.mode=='captain'){
                        $state.go('profile', {tab:4});
                    }
                    else{
                        $state.go('profile', {tab:3});
                    }
                    $rootScope.header.back = function () {
                        location.hash = '#/main';
                    }
                };
            }

            if($stateParams.mode){
                backToProfile();
            }

            $scope.scroll = function(ScrollData) {
                $timeout(function () {
                    scroll.element = document.querySelector('main');
                    scroll.set(ScrollData, 2);
                    ScrollData = 0;
                }, 100);
            };

            /*if ($stateParams.mode && $stateParams.mode != 0 && $stateParams.mode != 'captain')
                alert ('–í—ã–±–∏—Ä–∞–µ–º —Å–æ—Å–µ–¥–∞' + $scope.gender);*/

            $scope.openParticipant = function (id, name, lname) {
                //alert($stateParams.id);
                //alert(id);

                if(!$stateParams.mode || $stateParams.mode==0 || $stateParams.mode=='captain'){
                    var content = document.querySelector('main');
                    var sd=content.scrollTop;
                    $scope.loading = true;
                    var users = $scope.users.list;
                    //alert(JSON.stringify(users));
                    for (var i in users) {
                        if (users[i].id == id) {
                            $scope._participant = users[i];
                            //alert(id);
                            break;
                        }
                    }
                    $timeout(function () {
                        $scope.loading = false;
                    }, 1000);

                    $rootScope.header.simpleSearch = false;

                    $rootScope.header.back = function () {
                        $scope._participant = null;
                        $rootScope.header.simpleSearch = true;
                        //$scope.scroll(sd);

                        $timeout( function() {
                            //alert ('scroll');
                            $('main').animate({
                                scrollTop: $("#user-" + id).offset().top - 110
                            }, 500);

                        }, 1000);


                        $rootScope.header.back = function () {
                            location.hash = '#/main';
                        }
                    };
                    users = null;
                }
                else { //–≤—ã–±–∏—Ä–∞–µ–º —Å–æ—Å–µ–¥–∞

                        var confirm = $mdDialog.confirm()
                            .title('Confirm your choice')
                            .textContent('Are you sure that you want to choose '+name+' '+lname+' as your preferred roommate (priority'+$stateParams.mode+')?')
                            .ok('Yes')
                            .cancel('No');
                        $mdDialog.show(confirm).then(function (result) {
                            //–∑–∞–ø–∏—Å–∞—Ç—å –≤ —Å—Ç—Ä–æ–∫—É —Å –∞–π–¥–∏ my_id –≤ –ø–æ–ª–µ neighbor+$stateParams.mode id
                            var field = 'neighbor'+$stateParams.mode; //$stateParams.mode - 1 –∏–ª–∏ 2
                            var obj = {};
                            obj[field+''] = id+'/'+lname+' '+name
                            $rootScope['neighbor'+$stateParams.mode]=obj[field+''];
                            $state.go('profile', {tab:3});
                            //if (!$rootScope.vars.dev)
                            db.edit('participants', obj, $scope.my_id);

                        });
                }
            }
            if($stateParams.id && $stateParams.id!='none'){ //–µ—Å–ª–∏ –Ω–∞–∂–∞–ª–∏ –Ω–∞ —Å–æ—Å–µ–¥–∞ –≤ –ø—Ä–æ—Ñ–∏–ª–µ - –æ—Ç–∫—Ä—ã—Ç—å –µ–≥–æ –∫–∞—Ä—Ç–æ—á–∫—É
                $scope.$watch('users.list', function() {
                    if($scope.users.list.length>0){
                        $scope.openParticipant($stateParams.id);
                        backToProfile();
                    }
                });
            }
        }
    ]).factory('Users', ['$http', '$rootScope', '$timeout', function ($http, $rootScope, $timeout) {
    var Users = function () {
        this.items = [];
        this.list = [];
        this.step = 40;
        this.save_step = 40;
        this.busy = false;
    };

    Users.prototype.Init = function () {
        var reference = 'participants';
        var link = ('../files/' + reference + '.json?unique=' + new Date().getTime());
        //alert(link);
        return $http.get(link);
    };
    Users.prototype.nextPage = function () {
        if (this.busy) return;
        this.busy = true;
        console.log(this.step)
        if ($rootScope.search) {
            this.step = this.list.length;
            this.busy = false;
            return;
        } else {
            this.step = this.save_step;
        }
        if (this.items.length < 1) {
            this.Init().then(function (data) {
                var _data = data.data.values, filter = [], length = _data.length;
                var data_arr = [];
                for (var v in _data) {
                    var obj = _data[v];
                    obj['id'] = v;
                    obj['order']=_data[v]['order']?+_data[v]['order']:1000;
                    data_arr.push(obj);
                }
                this.list = data_arr;
                console.log('data_arr');
                console.log(data_arr);
                this.items = data_arr.slice(0, 40);
            }.bind(this));
        } else {
            var _list = this.items.length + 40;
            this.items = this.list.slice(0, _list);
            this.step += 20;
            this.save_step = this.step;
        }
        if (this.step == 40) {
            $timeout(function () {
                this.busy = false;
            }.bind(this), 1000)
        } else {
            this.busy = false;
        }
    };

    return Users;
}]).filter('search2', function () {
    return function (data, text) {
        text = text || '';
        text = text.toLowerCase();
        var out = [];
        if (text.length < 3) return data;
        for (var i = 0; i < data.length; i++) {
            //for (var i in data) {
            var fname = (data[i].hasOwnProperty("name")) ? data[i]["name"].toLowerCase() : "";
            var lname = (data[i].hasOwnProperty("lname")) ? data[i]["lname"].toLowerCase() : "";
            var flname = fname + " " + lname;
            if (fname.indexOf(text) > -1 ||
                lname.indexOf(text) > -1 ||
                flname.indexOf(text) > -1) {
                out.push(data[i])
            }
        }
        return out;
    };
});