angular.module('speakers', [])
    .controller('speakersCtrl', ['$rootScope', '$scope', '$state', '$timeout','db','$mdDialog','$stateParams',
        function ($rootScope, $scope, $state, $timeout, db, $mdDialog,$stateParams) {
            $rootScope.header.simpleSearch = true;
            if ($rootScope.search) $rootScope.search = '';

            $rootScope.header.moderation = true;

            //$rootScope.testmode='yes';

            //======–û–ø—Ä–µ–¥–µ–ª—è–µ–º testmode (–µ—Å–ª–∏ evp –∏–ª–∏ –æ—Ä–≥–∞–Ω–∏–∑–∞—Ç–æ—Ä - –¥–æ—Å—Ç—É–ø–Ω–∞ –º–æ–¥–µ—Ä–∞—Ü–∏—è)==========
            var my_id = localStorage.my_id?localStorage.my_id:false;
            if(my_id){
                db.get('participants',!$rootScope.vars.dev).then(function (participants) {
                    if(participants.values[my_id].status==2 || participants.values[my_id].status==3){
                        $rootScope.testmode='yes';
                    }
                });
            }
            //===================================================================================

                $rootScope.header.back = function(){
                    if(!$stateParams.id){
                        if($scope.info){
                            $scope.info=false;
                            $rootScope.header.simpleSearch = true;
                        }
                        else {
                            localStorage.animate=0;
                            location.hash = '#/main';
                        }
                    }
                    else{
                        // to agenda
                        if($stateParams.tab!='no')$state.go('agenda',{tab:$stateParams.tab});
                        else $state.go('news');
                    }
                };

                $scope.openSpeaker = function (speaker) {
                    $rootScope.header.simpleSearch = false;
                    $scope.info = speaker;
                }

                $scope.info = false;
                db.get('speakers',!$rootScope.vars.dev).then(function (data) {
                    var speakers = [];
                    for(var v in data.values){
                       var speaker = {};
                       speaker = data.values[v];
                       speaker['order'] = data.values[v]['order']?+data.values[v]['order']:1000;
                       speakers.push(speaker);
                    }
                    $scope.speakers = speakers;
                    if($stateParams.id){
                        $scope.openSpeaker(data.values[$stateParams.id]);
                    }
                });


            $scope.sendQuestion=function (question) {
                $timeout(function () {
                    var date = new Date();
                    var unixDate = Math.floor(date.getTime() / 1000);
                    var data={};
                    data['author'] = localStorage.name?localStorage.name:'';
                    data['author_email'] = $rootScope.vars.contacts_email?$rootScope.vars.contacts_email:'';
                    data['question'] = question;
                    data['date'] = unixDate;
                    data['speaker'] = $scope.info.last_name+' '+$scope.info.first_name;
                    data['speaker_email'] =  $scope.info.email;
                    data['moderation'] =  0;
                    //alert(JSON.stringify(data));
                    if(!$rootScope.vars.dev)db.add('questions',data);
                    $rootScope.question = '';
                    //===========BE–ë –ó–ê–ü–ò–°–¨ –í –¢–ê–ë–õ–ò–¶–£==============

                    //=============================================
                    $mdDialog.show(
                        $mdDialog.alert()
                            .title('Thank you!')
                            .textContent('Your question has been sent to the speaker')
                            .ok('–û–∫')
                    );
                },500);
            }

}]).filter('speakers_search', function () {
    return function (data, text) {
        //alert('111')
        text = text || '';
        text = text.toLowerCase();
        var out = [];
        if (text.length < 3) return data;
        for (var i = 0; i < data.length; i++) {
            //for (var i in data) {

            var fname = (data[i].hasOwnProperty("first_name")) ? data[i]["first_name"].toLowerCase() : "";
            var lname = (data[i].hasOwnProperty("last_name")) ? data[i]["last_name"].toLowerCase() : "";
            var flname = fname + " " + lname;
            if (fname.indexOf(text) > -1 ||
                lname.indexOf(text) > -1 ||
                flname.indexOf(text) > -1) {
                out.push(data[i])
            }
        }
        return out;
    };
});