angular.module('profile', ['ngMask'])
    .controller('profileCtrl', ['$rootScope', '$scope', '$state', '$timeout','$cookies','file','db','$stateParams','$mdDialog',
        function ($rootScope, $scope, $state, $timeout, $cookies,file,db,$stateParams,$mdDialog) {
            $scope.tab = $stateParams.tab?$stateParams.tab:1;
            //$scope.tab = 5;
            $scope.noInternet = false; //–≤—ã–≤–æ–¥–∏—Ç—å –Ω–∞–¥–ø–∏—Å—å "–ø—Ä–æ–≤–µ—Ä—å—Ç–µ —Å–æ–µ–¥–∏–Ω–µ–Ω–∏–µ —Å –∏–Ω—Ç–µ—Ä–Ω–µ—Ç–æ–º"
            $scope.showBtnSaveStatus = false;
            $scope.editStatus = false;

            var my_id = localStorage.my_id?localStorage.my_id:'';
            if(my_id){
                //console.log(my_id);
                db.get('participants',!$rootScope.vars.dev).then(function (participants) {
                    $scope.loading = true;
                    var participantsAll = participants.values;
                    var user = participants.values[my_id];
                    for(var key in user){
                        if(key!='transfers_to'){
                            $rootScope[key]=user[key];
                        }
                    }

                    /*console.log($rootScope);
                    console.log($scope.email);
                    console.log($scope.status);*/

                    if($rootScope.gender)localStorage.gender = $rootScope.gender;
                    $scope.chosenDish = $rootScope.dish?+$rootScope.dish:'no';

                    db.get('neighbors_status').then(function (data) {
                        for (var v in data.values){
                            $scope.neighborStatus = data.values[v].status;
                            break;
                        }
                        //console.log($scope['neighbor_final'][0]);
                        //console.log($scope.neighborStatus);
                        if($scope.neighborStatus==2 && $scope['neighbor_final'][0]){
                                var nf_id = $scope['neighbor_final'][0];
                                //console.log(participantsAll[nf_id]);
                                if(participantsAll[nf_id]){
                                    var nf = participantsAll[nf_id];
                                    var nf_name = nf_id+'/'+nf['lname']+' '+nf['name'];
                                    $rootScope['neighbor_final'] = nf_name;
                                }
                        }
                        $scope.loading = false;
                    });
                });
            }
            else
                $scope.noInternet = true;

            $scope.saveChangedStatus=function(mood){
                //alert(mood);
                $rootScope.mood = mood;
                $scope.editStatus = false;
                db.edit('participants', {'mood': mood}, my_id);
            }

            $scope.btnEditStatus = function () {
                $scope.editStatus = true;
            }

            $scope.chooseNeighbor=function(mode){
                $timeout(function () {
                    $state.go('participants',{mode:mode,id:'none'});
                },500)
            }

            $scope.goToNeighbor = function (neighbor) {
                var neighborId = neighbor.split('/')[0];
                if(neighbor){
                    $state.go('participants',{ mode:0,id:neighborId});
                }
            }
/*            $scope.goToCaptain = function (captainId) {
                $state.go('participants',{ mode:0,id:captainId});
            }*/
            $scope.noTravelTo = false;
            $scope.noTravelFrom = false;
            $scope.noTravelToMinsk = false;
            $scope.noTravelFromMinsk = false;
            $scope.getTravelData = function (to_or_from, noTravel, dynamic) {
                //–µ—Å–ª–∏ –Ω–µ—Ç –≤ –∫—É–∫–∞—Ö –∞–π–¥–∏ —Å—Ç—Ä–æ—á–∫–∏ —Ç—Ä–∞–Ω—Å—Ñ–µ—Ä–∞ - –ø—Ä–æ–π—Ç–∏—Å—å –ø–æ —Ç–∞–±–ª–∏—Ü–µ, –æ—Ç—ã—Å–∫–∞—Ç—å —Å–µ–±—è, –∏–Ω–∞—á–µ - –≤—ã—Ç–∞—â–∏—Ç—å —Å—Ç—Ä–æ—á–∫—É –ø–æ –∞–π–¥–∏ –∏–∑ –∫—É–∫–æ–≤
                //–µ—Å–ª–∏ —Å—Ç—Ä–æ—á–∫–∏ –≤ —Ç–∞–±–ª–∏—Ü–µ –Ω–µ—Ç - noTravel = true (–≤—ã–≤–µ—Å—Ç–∏ —Ñ–æ—Ä–º—É –¥–ª—è –∑–∞–ø–æ–ª–Ω–µ–Ω–∏—è), –ø—Ä–∏ –æ—Ç–ø—Ä–∞–≤–ª–µ–Ω–∏–∏ –∫–æ—Ç–æ—Ä–æ–π —Å–æ–∑–¥–∞–µ—Ç—Å—è –Ω–æ–≤–∞—è —Å—Ç—Ä–æ–∫–∞ –≤ —Ç–∞–±–ª–∏—Ü–µ —Ç—Ä–∞–Ω—Å—Ñ–µ—Ä–æ–≤, –∑–∞–Ω–µ—Å—Ç–∏ –¥–∞–Ω–Ω—ã–µ –≤ $scope
                db.get(to_or_from,dynamic).then(function (transfers) {
                    //if(!$cookies.get('transfers_to')){
                    if(!localStorage[to_or_from]){
                        for(var v in transfers.values) {
                            if (transfers.values[v].participant.indexOf(my_id) >= 0) {
                                $scope[to_or_from] = transfers.values[v];
                                //$cookies.put('transfers_to', v, {expires: $rootScope.expireDate});
                                localStorage[to_or_from] = v;
                                break;
                            }
                        }
                        if(!$scope[to_or_from]){
                            $scope[noTravel] = true;
                            $scope[to_or_from]={};
                        }
                    }
                    else{
                        $scope[to_or_from] = transfers.values[localStorage[to_or_from]];
                    }
                },function (error) {
                    $scope.noTravelTo = false;
                    $scope.noTravelFrom = false;
                });
            }

            //==================–¢—Ä–∞–Ω—Å—Ñ–µ—Ä—ã –ú–æ—Å–∫–≤–∞=================================
            $scope.getTravelData('transfers_to','noTravelTo', !$rootScope.vars.dev);
            $scope.getTravelData('transfers_from','noTravelFrom',!$rootScope.vars.dev);
            //===================================================================


            //==================–¢—Ä–∞–Ω—Å—Ñ–µ—Ä—ã –ú–∏–Ω—Å–∫==================================
            $scope.getTravelData('transfers_tominsk','noTravelToMinsk',false);
            $scope.getTravelData('transfers_fromminsk','noTravelFromMinsk',false);
            //===================================================================


            $scope.toggleWay = function (way,transfer) {
                $scope[transfer]['way'] = way;
            }

            var msgFillAllFields=function () {
                $mdDialog.show(
                    $mdDialog.alert()
                        .title('It is required that you fill all the fields')
/*                        .textContent('All fields should be filled obligatory!')*/
                        .ok('–û–∫')
                );
            }


            var addTravelDataToTable=function (obj,table) {
                var confirm = $mdDialog.confirm()
                    .title('Please note that transfer data cannot be modified after submitting! Do you want to continue?')
                    .ok('Yes')
                    .cancel('No');
                $mdDialog.show(confirm).then(function(result) {
                    var data={};
                    for(var key in obj){
                        if(key!='way'){
                            data[key] = obj[key];
                        }
                    }
                    var participant = [];
                    participant.push(my_id);
                    data['participant'] = participant;
                    data['user'] = localStorage.name?localStorage.name:'';
                    data['user_email'] = $rootScope.vars.contacts_email?$rootScope.vars.contacts_email:'';
                    //alert(JSON.stringify(data));

                    db.add(table,data);
                    //===========BE–ë –ó–ê–ü–ò–°–¨ –í –¢–ê–ë–õ–ò–¶–£==============

                    //=============================================
                    $mdDialog.show(
                        $mdDialog.alert()
                            .title('The data has been sent successfully. Thank you!')
                            .ok('–û–∫')
                    );
                    if(table=="transfers_to"){
                        $scope.noTravelTo = false;
                    }
                    else{
                        $scope.noTravelFrom = false;
                    }
                });
            }
            $scope.sendTravelCard = function(transfer, table){
                if(transfer.way){
                    if(transfer.way=='air'){
                        if(transfer.date && transfer.time && transfer.airport && transfer.flight){
                            addTravelDataToTable(transfer,table);
                        }
                        else{
                            msgFillAllFields();
                        }
                    }
                    else{
                        if(transfer.date && transfer.time && transfer.station && transfer.flight && transfer.car){
                            addTravelDataToTable(transfer,table);
                        }
                        else{
                            msgFillAllFields();
                        }
                    }
                }
                else{
                    msgFillAllFields();
                }
            }
            
            var getCaptain = function (number) {
                //alert(number);
                db.get('captains').then(function (captains) {
                    for(var v in captains.values){
                        if(captains.values[v].number==number){
                            $scope.captain = captains.values[v].captain;
                            $scope.captainId = captains.values[v].captain_id;
;                            break;
                        }
                    }
                });
            }
            $scope.showTab = function (tab) {
                $scope.tab = tab;
                if(tab==4 && !$scope.captain) getCaptain($scope.command);
            }

            $scope.showTab($scope.tab);
            
            $scope.addPhoto = function(){
                file.attach();
            };

            $scope.removeAvatar = function() {
                $rootScope.photo = false;
                $scope.$applyAsync();
                db.edit('participants',{'photo':''},my_id);
            };

            //================–ú–æ–µ –º–µ–Ω—é=======================
            $scope.chooseDish = function (dish) {
                if($rootScope.dish!=0 && $rootScope.dish!=1)$scope.chosenDish = dish;
            }
            $scope.sendDish = function () {
                $rootScope.dish = $scope.chosenDish;
                db.edit('participants',{'dish':$scope.chosenDish},my_id);
                $mdDialog.show(
                    $mdDialog.alert()
                        .title('Thank you for your choice!')
                        .textContent('We will take it into account')
                        .ok('–û–∫')
                );
            }
            //===============================================

        }]);