angular.module('questions_to_orgs', [])
    .controller('questions_to_orgsCtrl', ['$scope', '$stateParams', '$http', 'db', '$rootScope','$state','$mdDialog','$filter', '$timeout',
        function ($scope, $stateParams, $http, db, $rootScope,$state, $mdDialog, $filter, $timeout) {
            $scope.loading = true;
            //console.log($stateParams);

            $scope.$parent.header.back = function () {
                $state.go('info', {section: 'orgs'});
            };


            db.get('orgs').then(function (data) {
                if ($stateParams.id) {
                    if (data.values.hasOwnProperty($stateParams.id)) {
                        $scope.speaker = data.values[$stateParams.id];
                        $scope.loading = false;
                    }

                    //console.log($scope.speaker);
                }
            });

            $scope.sendForm = function(fquestion) {
                var obj = {};
                obj['question'] = fquestion;
                obj['date'] = $filter('date')(new Date(), 'yyyy-MM-dd HH:mm:ss');

                if ($scope.speaker['lname'] && $scope.speaker['name'])
                    obj['org'] = $scope.speaker['name'] + ' ' + $scope.speaker['lname'];

                obj['author'] = localStorage.name?localStorage.name:'';
                obj['author_email'] = $rootScope.vars.contacts_email?$rootScope.vars.contacts_email:'';

                $scope.table_data = obj;
                //console.log($scope.table_data);

                var message = obj['date'] + '<br><b>–û—Ç –∫–æ–≥–æ: </b>' + obj['author'] + ' (' + obj['author_email'] + ')<br><br>' + obj['question'];
                //console.log(message);


                $scope.question_text = '';
                $timeout(function(){
                    $mdDialog.show(
                        $mdDialog.alert()
                            .title('Thank you')
                            .textContent("Your question has been sent!")
                            .ok('–û–ö')
                    );
                    $scope.question = '';
                }, 300);
            };
        }
    ]);
