angular.module('app.controllers', [])

.controller('globalCtrl', ['$rootScope', 'scroll', '$timeout','$scope', function($rootScope, scroll, $timeout,$scope) {
  $rootScope.scrollTop = function(){
    scroll.element = document.querySelector('main');
    scroll.animateTo('top', 0);
  };
  $scope.animateTimeout = function(callback) {
    if (!callback) return;
    $timeout(function(){
      callback();
    }.bind(this),100);
  };

  var now = new Date();
  $rootScope.expireDate=(new Date(now.getFullYear()+1, now.getMonth(), now.getDate())).toGMTString();
  
  $rootScope.$on('$stateChangeSuccess',
        function (event, toState, toParams, fromState, fromParams) {
            if (fromState['name'] === "") return;
            var data = {}, params = [];
            for (var p in fromParams) {
                params.push(fromParams[p]);
            }
            var s=(params.length)?params.join("&"):'';
            data.url = '?/#!/'+fromState['name']+ '/'+s;
            if(toState['name'] == 'main'){
            }
        }
    );

  // Gallery method
  $scope.firstTime = false;
  $scope.showFancy= function (data) {
    var pictures = angular.element(data.currentTarget).parents(".docs-pictures")[0];
    angular.element('body').addClass('overhidden');
    var viewer;
    var options = {
      //inline: true,
      url: 'data-original',
      ready:  function (e) {
        // console.log(e.type);
        if($scope.firstTime){
          viewer.destroy();
          $scope.firstTime = false;
          angular.element(angular.element(data.currentTarget)).trigger("click")
        }
        else{
          $scope.firstTime = true;
        }
      },
      show:  function (e) {
        // console.log(e.type);
      },
      shown:  function (e) {
        // console.log(e.type);
      },
      hide:  function (e) {
        // console.log(e.type);
        angular.element('body').removeClass('overhidden');
      },
      hidden:  function (e) {
        // console.log(e.type);
      },
      view:  function (e) {
        // console.log(e.type, e.detail.index);
      },
      viewed:  function (e) {
        // console.log(e.type, e.detail.index);
        // this.viewer.zoomTo(1).rotateTo(180);
      }
    };
    viewer = new Viewer(pictures, options);
  }
}])

.controller('menuCtrl', ['$rootScope','$scope', '$timeout','$state', function($rootScope,$scope, $timeout,$state) {
  $scope.items = pageOptions;
  $scope.goToPage=function (page) {
    $state.go(page);
  }
  $scope.close = function(){
    $timeout(function(){
      $rootScope.navDrawer.close()
    }, 500);
  }
}])

.controller('headerCtrl', ['$rootScope', '$scope', '$timeout', '$state', function($rootScope, $scope, $timeout, $state) {
  var page = pageOptions.filter(function(n){return n.id == $state.current.name})[0];
  $rootScope.header = {title:page?page.title:''};
  $rootScope.header.back = function(){
    localStorage.animate=0;
    location.hash = '#/main';
  };
  $rootScope.noAnimation = true;
}]);