angular.module('notices', [])
  .controller('noticesCtrl', ['$rootScope', '$scope', 'db', 'unixDate', '$cookies',
    function($rootScope, $scope, db, unixDate, $cookies) {
      $scope.unixTimeStamp = unixDate.getNowStamp();
      $scope.CurrUserEmail = $rootScope.vars.contacts_email?$rootScope.vars.contacts_email.trim():'';
     // –ø–æ–ª—É—á–∞–µ–º –º–∞—Å—Å–∏–≤ –≤—Å–µ—Ö —É–≤–µ–¥–æ–º–ª–µ–Ω–∏–π
      db.get('notices').then(function (resp) {
        $scope.headers = resp.headers;
        var pushes = [], push = {}, id = 0;

        for (var i in resp.values) {
            push = resp.values[i];
            push['id'] = id;
            push['guid'] = i;
            push['date_public_human'] = unixDate.toHumanFormat(push['date_public']);
            push['date_public'] = +push['date_public'];
            pushes.push(push);

            if( push['date_public'] < $scope.unixTimeStamp &&
                (push['users'].indexOf($scope.CurrUserEmail) != -1 ||
                    push['users'].length == 0)) {
                id += 1;
            }
        }

        localStorage["push_count"] = id;
        //$cookies.put('push_count', id+'', {expires: $rootScope.expireDate});
        $scope.alarms = pushes;
        console.log('$scope.alarms', $scope.alarms);
      });
    }
  ]);
