angular.module('app.directives', [])
    .directive("scrolltop", function ($window) {
        return function (scope, element, attrs) {
            angular.element(document.querySelector("main")).bind("scroll", function () {
                scope.scrolled = (this.scrollTop >= window.innerHeight * 0.1);
                scope.$apply();
            });
        };
    })
    .directive('hello', ['$timeout',function($timeout){
        return {
            restrict: 'A',
            link: function(scope, element, attr) {
                $(element).addClass('active');
            }
        }
    }])
    .directive('onLongPress', function ($timeout) {
        return {
            restrict: 'A',
            link: function ($scope, $elm, $attrs) {
                $elm.bind('touchstart', function (evt) {
                    // Locally scoped variable that will keep track of the long press
                    $scope.longPress = true;
                    // We'll set a timeout for 600 ms for a long press
                    $timeout(function () {
                        if ($scope.longPress) {
                            // If the touchend event hasn't fired,
                            // apply the function given in on the element's on-long-press attribute
                            $scope.$apply(function () {
                                $scope.$eval($attrs.onLongPress)
                            });
                        }
                    }, 1000);
                });
                $elm.bind('touchend', function (evt) {
                    // Prevent the onLongPress event from firing
                    $scope.longPress = false;
                    // If there is an on-touch-end function attached to this element, apply it
                    if ($attrs.onTouchEnd) {
                        $scope.$apply(function () {
                            $scope.$eval($attrs.onTouchEnd)
                        });
                    }
                });
            }
        };
    })
    .directive('loading', function () {
        return {
            restrict: 'E',
            replace: true,
            template: '<div class="loading"><div><svg width="108px" height="108px" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100" preserveAspectRatio="xMidYMid" class="uil-clock"><rect x="0" y="0" width="100" height="100" fill="none" class="bk"></rect><circle cx="50" cy="50" r="30" fill="#fff" stroke="#ed1d24" stroke-width="8px"></circle><line x1="50" y1="50" x2="50" y2="30" stroke="#3c302e" stroke-width="5" stroke-linecap="round"><animateTransform attributeName="transform" type="rotate" from="0 50 50" to="360 50 50" dur="1s" repeatCount="indefinite"></animateTransform></line><line x1="50" y1="50" x2="50" y2="20" stroke="#fefefe" stroke-width="2px" stroke-linecap="round" opacity="0"><animateTransform attributeName="transform" type="rotate" from="0 50 50" to="360 50 50" dur="0.2s" repeatCount="indefinite"></animateTransform></line></svg><h1>LOADING...</h1></div></div>',
            link: function (scope, element, attr) {
                scope.$watch('loading', function (val) {
                    if (val)
                        $(element).show();
                    else
                        $(element).hide();
                });
            }
        }
    })
    .directive("epButton", ['$timeout', '$state',
    function ($timeout, $state) {
        return function (scope, element, attrs) {
            var delayDefaultMs = 400,
                durationDefaultMs = 800,
                delayMs = attrs.delayMs ? attrs.delayMs : delayDefaultMs,
                durationMs = attrs.durationMs ? attrs.durationMs : durationDefaultMs,
                buttonElem = angular.element("<md-button class='ep-button md-button md-raised button_" + attrs.order + "'>" + attrs.content + "</md-button>"),
                styleElem = angular.element("<style>." + attrs.effectName + "{animation-duration: " + durationMs + "ms !important; -webkit-animation-duration: " + durationMs + "ms !important;</style>");
            element.append(buttonElem);
            element.append(styleElem);
            buttonElem.bind('click', function () {
                buttonElem.addClass(attrs.effectName);
                var self = buttonElem;
                $timeout(function () {
                    self.removeClass(attrs.effectName);
                    $state.go(attrs.link);
                }, delayMs);
                $timeout(function () {
                    if (attrs.callbackName) {
                        scope.$eval(attrs.callbackName);
                    }
                }, delayMs * 1.2);
            });

            /*
             example markup:
             <div
             ep-button
             link="{{item.id}}"
             delay-ms="400"
             duration-ms="800"
             content="—Ü–≤–µ—Ç —Ñ–æ–Ω–∞"
             effect-name="bg-change"
             callback-name="attention()"
             />

             effects list:
             bg-change
             rotate
             rounded
             opacity
             pulse
             shadow
             rubberBand
             flipInY
             zoomOut
             wobble

             more effects here https://daneden.github.io/animate.css/
             */
        }
    }
]);